<template>
  <section class="blog" v-if="this.blog">
    <div class="blog__header">
      <p class="blog__header--title">{{this.blog[this.id].thumb.txt}}</p>
      <span class="blog__header--title-span">{{this.blog[this.id].thumb.bold}}</span>
    </div>
    <div class="blog__links">
      <div class="blog__links-link"
        v-for="(artcle, idx) in this.blog[this.id].articles" 
        @click="selectArticle(idx)" :key="idx"
        :class="{'blog__links-link--selected': idx === index}"
      >
        <div class="blog__links-link--img" :style="{'background-image': 'url('+artcle.img+')'}"></div>
        <p class="blog__links-link--txt">{{artcle.title}}</p>
      </div>
        
    </div>
    <div class="blog__container">
      <h3 class="blog__container--title">{{this.blog[this.id].articles[this.index].title}}</h3>
      <div class="blog__container--img" :style="{'background-image': 'url('+this.blog[this.id].articles[this.index].img+')'}"></div>
      <p class="blog__container--txt" v-html="this.blog[this.id].articles[this.index].text"></p>

    </div>
    <!-- <h1>{{this.blog[this.id].articles[this.index].title}}</h1>
    <h3 v-html="this.blog[this.id].articles[this.index].text"></h3> -->
  </section>
</template>
<script>
export default {
  data() {
    return {
      blog: null,
      id: 0,
      index: 0
      // articles: []
    }
  },
  beforeMount() {
    this.blog = [...this.$store.state.data.blog];
    this.id = Number(this.$route.params.postId);
    this.index = Number(this.$route.params.index);
    // console.log(this.blog[this.id].articles[this.index])
    // this.articles = [...this.data.blog.articles]
    // console.log('this.$route.hash', this.$route.name)
  },
  mounted () {
    // console.log('this.$route.params', this.$route.params)
  },
  methods: {
    selectArticle(idx) {
      console.log('selectArticle', idx, this.index)
      console.log('selectArticle if', idx != this.index)
      if(idx != this.index) {
        this.index = idx;
        this.$router.push(`/blog/${this.id}/${idx}`);
      }
    }
  }
}
</script>
